@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600&display=swap);
* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  text-decoration: none;
  list-style: none;
}

a:link {
  color: inherit;
}

a:visited {
  color: inherit;
  background-color: transparent;
  text-decoration: none;
}

a:hover {
  color: rgb(208, 156, 11);
  color: var(--color-hover);
}

:root {
  --color-background-dark-mode: rgba(0, 0, 0, 1);
  --color-background-light-mode: rgba(255, 235, 205);
  --color-background-shadow-mode: rgba(255, 255, 255);

  --color-primary-dark-mode: rgba(255, 235, 205);
  --color-primary-light-mode: rgb(41, 28, 28);
  --color-primary-shadow-mode: rgba(255, 255, 255);

  --color-dark-mode: rgba(255, 255, 255);
  --color-light-light-mode: rgb(12, 2, 32);
  --color-shadow-mode: rgba(255, 255, 255);

  --color-hover: rgb(208, 156, 11);

  --container-width-large: 75%;
  --container-width-medium: 86%;
  --container-width-small: 90%;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: 'Poppins', sans-serif;
  -webkit-text-size-adjust: 100%;
}

.dark-mode .active {
  border-radius: 2rem;
  background: rgb(0, 0, 0);
  box-shadow: 0.1rem 0.1rem 0.5rem 0.1rem rgb(208, 156, 11);
  transition: all 1s ease-out;
}

.light-mode .active {
  border-radius: 2rem;
  background: rgb(221, 226, 162);
  box-shadow: 1rem 1rem 1rem rgb(188, 192, 138);
  transition: all 1s ease-out;
}

.dark-mode {
  background-color: rgba(0, 0, 0, 1);
  background-color: var(--color-background-dark-mode);
  color: rgba(255, 235, 205);
  color: var(--color-primary-dark-mode);
}

.light-mode {
  background-color: rgba(255, 235, 205);
  background-color: var(--color-background-light-mode);
  color: rgb(41, 28, 28);
  color: var(--color-primary-light-mode);
}

.light-mode .box-shadow {
  border-radius: 3rem;
  background: rgb(221, 226, 162);
  box-shadow: 1rem 1rem 1rem rgb(189, 193, 139);
}

.dark-mode .box-shadow {
  border-radius: 3rem;
  background: rgb(0, 0, 0);
  box-shadow: 0.1rem 0.5rem 1rem 0.1rem rgb(208, 156, 11);
}

.section {
  height: 100vh;
  max-width: 1450px;
  margin: 0 auto;
}

.dark-mode {
  background-color: rgba(0, 0, 0, 1);
  background-color: var(--color-background-dark-mode);
}

.section-title {
  margin: 1rem auto;
  text-align: center;
  font-size: 2rem;
  font-weight: 200;
  text-transform: uppercase;
  letter-spacing: 1rem;
}

@media screen and (max-width: 980px) {
  .section {
    height: auto;
  }

  .section-title {
    font-size: 1.5rem;
    font-weight: 200;
    text-transform: uppercase;
    letter-spacing: 0.5rem;
  }
}

.contacts {
  width: 15rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.hero-section {
  padding-top: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.about-title-container {
  margin: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  text-align: center;
}

.hero-greeting-title {
  font-size: 2rem;
  font-weight: 200;
  text-transform: uppercase;
  letter-spacing: 1.5rem;
}

.hero-title {
  margin: 3rem auto;
  font-size: 1rem;
  font-weight: 200;
  text-transform: uppercase;
  letter-spacing: 0.3rem;
}

.hero-image-container {
  max-height: 35rem;
  margin-top: 10rem;
  margin: 2rem;
}

.hero-image {
  max-height: 35rem;
  border-radius: 3rem;
}

.download-CV {
  max-width: 20rem;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding: 2rem 4rem;
  margin-top: 2rem;
}

@media screen and (max-width: 1150px) {
  .hero-image-container {
    margin: 2rem;
  }

  .hero-image-container {
    max-height: 25rem;
  }

  .hero-image {
    max-height: 25rem;
  }
}

@media screen and (max-width: 800px) {
  .hero-section {
    display: flex;
    flex-direction: column;
  }
  .hero-image {
    max-height: 32rem;
    object-fit: contain;
  }
  .about-title-container {
    padding-top: 4rem;
  }
}

@media screen and (max-width: 480px) {
  .hero-greeting-title {
    font-size: 1.2rem;
    font-weight: 200;
    text-transform: uppercase;
    letter-spacing: 1rem;
  }
  .hero-section {
    display: flex;
    flex-direction: column;
  }
  .hero-image {
    max-height: 25rem;
    object-fit: contain;
  }
  .about-title-container {
    padding-top: 4rem;
  }
}

.projects-section-container {
  margin-top: 4rem;
}

.projects-section {
  padding: 2rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
  grid-row-gap: 2rem;
  row-gap: 2rem;
}

.project-card {
  margin: auto;
  text-align: center;
  max-height: 35rem;
  max-width: 37rem;
}

.project-card-image-container {
  position: relative;
  max-height: 30rem;
  max-width: 37rem;
  overflow: hidden;
  border-top-left-radius: 3rem;
  border-top-right-radius: 3rem;
}

.project-card-image {
  width: 100%;
  object-fit: cover;
}

.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  background-color: rgb(221, 226, 162, 0.8);

  transition: -webkit-transform 2s;

  transition: transform 2s;

  transition: transform 2s, -webkit-transform 2s;
}

.project-card-image:hover {
  -webkit-transform: scale(1.2);
          transform: scale(1.2);
  transition-duration: 2s;
}

.project-card-image-container:hover .overlay {
  -webkit-transform: translateY(0);
          transform: translateY(0);
  box-shadow: 0px 1px 1px rgba(0, 0, 0, 0.12), 0px 4px 4px rgba(0, 0, 0, 0.06),
    1px 4px 6px rgba(0, 0, 0, 0.16);
}

.project-card-interface {
  overflow: hidden;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.project-card-button {
  padding: 1rem 2rem;
  margin: 0 1rem;
}

@media screen and (max-width: 980px) {
  .projects-section {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}

@media screen and (max-width: 480px) {
  .project-card {
    max-height: 25rem;
  }
  .project-card-image-container {
    max-height: 20rem;
  }
}

.footer {
  height: 6rem;
  display: flex;
  padding-right: 10rem;
  padding-left: 10rem;
  justify-content: space-evenly;
  border-top: 0.01rem solid black;
}

.dark-mode .footer {
  border-top: 0.01rem solid white;
}

.footer-left-side {
  margin: auto;
  margin-left: 0;
}

.header-container {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  height: 5rem;
  width: 100%;
  position: fixed;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
  border-bottom: 0.01rem solid black;
  z-index: 10;
}

.navbar-list-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-transform: uppercase;
  letter-spacing: 0.5rem;
}

.navbar-list-container-mobile-view {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100vw;
  height: 60rem;

  text-align: center;
  text-transform: uppercase;
  letter-spacing: 0.5rem;
  background-color: rgba(255, 235, 205, 0.9);

  transition: -webkit-transform 2s;

  transition: transform 2s;

  transition: transform 2s, -webkit-transform 2s;
  /* transition: all 1.5s ease-out; */
}

.dark-mode .navbar-list-container-mobile-view {
  background-color: rgb(0, 0, 0, 0.9);
}

.navbar-list-container-mobile-view .link {
  display: inline-block;
  margin-top: 3rem;
}

.burger-menu-button {
  display: none;
}

.dark-mode .navbar-list-container {
  border-bottom: 0.01rem solid white;
}

.link {
  padding: 0.5rem 2rem;
}

.link,
.mode-control-button {
  cursor: pointer;
}

.mode-interface-container {
  margin: 0 3rem;
  width: 6rem;
  display: flex;
  align-items: baseline;
  justify-content: space-evenly;
}

.mode-control-button,
.burger-menu-button {
  background-color: inherit;
}

.mode-control-button:hover {
  color: rgb(208, 156, 11);
}

.dark-mode .mode-control-button {
  color: rgba(255, 235, 205);
}

.dark-mode .mode-control-button:hover {
  color: rgb(208, 156, 11);
}

@media screen and (max-width: 1000px) {
  .link {
    padding: 0.5rem 1rem;
  }
}
@media screen and (max-width: 870px) {
  .navbar-list-container {
    display: none;
  }
  .burger-menu-button {
    display: block;
    margin: 0 3rem;
  }
  .header-container {
    justify-content: space-between;
    padding: 0 1rem;
  }
}

