  .navbar-list-container {
    display: none;
  }
  .burger-menu-button {
    display: block;
  }
} */

.experience-section {
  margin: 0 auto;
}

.tools-grid-area {
  margin-top: 2rem;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-row-gap: 1rem;
  row-gap: 1rem;
}

.tool-card {
  padding: 2rem 3rem;
  margin: 1rem;
  text-align: center;
}

.tool-image {
  margin: auto;
  max-height: 10rem;
  object-fit: cover;
}

@media screen and (max-width: 1150px) {
  .tool-card {
    max-height: 14rem;
    max-width: 14rem;
  }

  .tool-image {
    max-height: 7rem;
    max-width: 7rem;
  }
}

@media screen and (max-width: 980px) {
  .tools-grid-area {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

@media screen and (max-width: 725px) {
  .tools-grid-area {
    grid-template-columns: 1fr 1fr;
  }
}

@media screen and (max-width: 480px) {
  .tool-card {
    max-height: 10rem;
    max-width: 10rem;
  }

  .tool-image {
    max-height: 4rem;
    max-width: 4rem;
  }
  .tool-card {
    padding: 2rem 3rem;
    margin: 0.8rem;
    text-align: center;
  }
  .tools-grid-area {
    grid-row-gap: 0;
    row-gap: 0;
  }
}

.experience-section {
  display: flex;
  margin-top: 3rem;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.experience-card {
  display: flex;
  margin: 3rem auto;
  max-width: 60rem;
  height: 15rem;
}

.card-image-container {
  margin: auto;
  margin-left: 2rem;
}

.card-description-container {
  margin: 1rem 2rem;
}

.card-image {
  display: block;
  width: 15rem;
  height: 10rem;
  object-fit: cover;
}

.card-title {
  margin: 2rem 4rem;
}

.card-description {
  margin: 0 2rem;
}

@media screen and (max-width: 980px) {
  .experience-card {
    max-width: 40rem;
  }

  .card-description-container {
    margin: 1rem 1rem;
  }

  .card-image {
    display: block;
    width: 15rem;
    height: 10rem;
    object-fit: cover;
  }

  .card-title {
    margin: 1rem 2rem;
  }

  .card-description {
    margin: 0 1rem;
  }
}

@media screen and (max-width: 660px) {
  .experience-card {
    max-width: 28rem;
  }

  .card-image {
    width: 10rem;
    height: 7rem;
  }

  .card-title {
    margin: 0.5rem 1rem;
    font-size: 1rem;
  }

  .card-description {
    margin: 0 1rem;
    font-size: 0.8rem;
  }
}

@media screen and (max-width: 480px) {
  .experience-card {
    display: flex;
    flex-direction: column;
    margin: 2rem auto;
    max-width: 22rem;
    height: 22rem;
  }
  .card-image-container {
    margin: auto;
  }
  .card-image {
    width: 15rem;
    height: 8rem;
  }
}

